import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AppState, FlashMessage } from './types';

const APP_SLICE_NAME = 'app';

const initialState: AppState = {
  isDarkMode: false,
  isOpenMenu: true,
  isSmallWidth: false,
  redirectUrl: null,
  flashMessage: null,
};

export const appSlice = createSlice({
  name: APP_SLICE_NAME,
  initialState,
  reducers: {
    toggleDarkMode: (state) => {
      state.isDarkMode = !state.isDarkMode;
    },
    setIsDarkMode: (state, action: PayloadAction<boolean>) => {
      state.isDarkMode = action.payload;
    },
    setIsOpenMenu: (state, action: PayloadAction<boolean>) => {
      state.isOpenMenu = action.payload;
    },
    toggleMenuMode: (state) => {
      state.isOpenMenu = !state.isOpenMenu;
    },
    setIsSmallWidth: (state, action: PayloadAction<boolean>) => {
      state.isSmallWidth = action.payload;
    },
    //   RedirectExecutor   
    // redirectUrl    redirect
    //         
    //       
    //     HISTORY API
    redirect: (state, action: PayloadAction<string>) => {
      state.redirectUrl = {
        path: action.payload,
      };
    },

    addFlashMessage: (state, action: PayloadAction<FlashMessage>) => {
      state.flashMessage = action.payload;
    },
  },
});
